@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin mediumdesk {
  @media (max-width: (1600px)) {
    @content;
  }
}

@mixin minidesk {
  @media (max-width: (1300px)) {
    @content;
  }
}

@mixin smalldesk {
  @media (max-width: (1100px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin minimobile {
  @media (max-width: (424px)) {
    @content;
  }
}