.animation-class {
   opacity: 1;
   visibility: visible;
   transform: translate(0);
   &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4), &:nth-child(5) {
     opacity: 1 !important;
     visibility: visible !important;
     transform: translate(0) !important;
  } 
 }