.plan {

   &__section {
      background-color: #080a0f57;
      padding-top: 60px;
      padding-bottom: 100px;
      position: relative;
   }

   &__decor {
      &-hat {
         position: absolute;
         max-width: 700px;
         left: -100px;
         top: 40%;
         animation: rotate2 30s infinite, moveLeftRight 10s infinite;       
       
         @include minidesk {
            width: 500px;
         }

         @include smalldesk {
            position: static;
            width: 300px;         }

      }
   }

   &__body {
      display: flex;
      flex-direction: column;
      align-items: end;

      @include smalldesk {
         align-items: center;
         justify-content: center;
      }
   }

   &__items {
      margin-top: 120px;
      max-width: 650px;

      @include smalldesk {
         margin-top: 30px;
      }
   }

   &__item {
      background: #201F23;
      box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.5);
      border-radius: 10px;
      opacity: 0;
      &:nth-child(1) {
         transform: translateX(80px);
         transition: 1s;
      }
      &:nth-child(2) {
         transform: translateX(90px);
         transition: 2s;
      }
      &:nth-child(3) {
         transform: translateX(100px);
         transition: 2.5s;
      }
   }

   &__text {
      padding-left: 45px;
      padding-bottom: 30px;
      padding-right: 55px;
      color: var(--color-white);
      font-size: 18px;
      line-height: 24px;
   }

   &__title {
      padding-left: 45px;
      padding-top: 40px;
      margin-bottom: 30px;
   }
}