.hero {
  padding-top: 100px;
  height: calc(100vh - 82px);
  background-color: #232324;
  position: relative;
  overflow: hidden;
  @media (max-width: 991px) {
    padding-top: 40px;
  }
  &__inner {   
    max-width: 720px;
    padding-top: 70px;
    padding-bottom: 60px;  
  }
        
  &__title {
    text-transform: none;
    font-family: "RobotoMono-Light", sans-serif;
    font-weight: 300;
    color: var(--color-white);
    letter-spacing: 1.5px; 
  }
    
  &__descr {
   margin-bottom: 50px;
   font-weight: 300;
   font-size: 22px;
   line-height: 24px;
   color: #FFFFFF;   
  
  
   @include mobile {
     font-size: 14px;
   }
 }

 &__btn {
   padding: 18px 65px;
   position: relative;
  &::before {
    position: absolute;
    content: '';
    width: 20px;
    height: 100%;
    left: 0;
    top: 0;
    background-color:rgba(#fff, 0.6);
    animation: flash 4s infinite;
    transition: .5s;
  }

   @include mobile {
     width: 100%;
   }
 }

 &__decor {
   &-glasses {
     position: absolute;
     left: 0;
     top: -45px;
    animation: rotate 10s infinite;
     @include minidesk {
       width: 100px;
     }   
   }

   &-helmet-blue {
     position: absolute;
     top: -40px;
     left: 55%;
     animation: moveLeftRight 10s infinite , rotate 10s infinite;    
     @include mobile {
       width: 110px;
       top: 6%;
       left: 90%;
     }

     @include minimobile {
       display: none;
     }
   }

   &-joker {
     position: absolute;
     bottom: -20px;
     right: 50px;
     animation: scale 30s infinite;
     @include mediumdesk {
       width: 500px;
       bottom: -2px;
     }

     @include minidesk {
       width: 350px;
     }

     @include smalldesk {
       right: 0;
     }     

     @include mobile {
       width: 250px;
     }
     @media (max-width: 1000px) {
      width: 200px;
     }
     @media (max-width: 600px) {
      display: none;
     }
   }

   &-helmet-red {
     position: absolute;
     bottom: -100px;
     left: -20px;
     animation: moveBack 10s infinite;
     @include minidesk {
       width: 100px;
       bottom: -60px;
     }     

   }
 }
}

@keyframes flash {
  0% {
    transform: skewX(-45deg) translateX(40px);
  }
  10% {
    transform: skewX(-45deg) translateX(300px);
  }
  11% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }

}

@keyframes rotate {
  0% {
    transform: rotate(15deg);
  }
  25% {
    transform: rotate(25deg);
  }
  50% {
    transform: rotate(45deg);
  }
  75% {
    transform: rotate(35deg);
  }
  100% {
    transform: rotate(15deg);
  }
}


@keyframes rotate2 {
  0% {
    transform: rotate(10deg);
  }
  25% {
    transform: rotate(20deg);
  }
  50% {
    transform: rotate(-5deg);
  }
  75% {
    transform: rotate(35deg);
  }
  100% {
    transform: rotate(15deg);
  }
}

@keyframes moveLeftRight {
  0% {
    transform: translateX(0deg);
    margin-left: 10px;
   }
  25% {
    transform: translateX(30deg);
    margin-left: 30px;
  }
  50% {
    transform: translateX(-10deg);
    margin-left: -10px;
   }
  75% {
    transform: translateX(20deg);
    margin-left: 20px;
  }
  100% {
    transform: translateX(0deg);
    margin-left: 10px;
  }
}

@keyframes scale {
  0% {
    transform: scale(1);
   }
  25% {
    transform: scale(1.2);
  }
  50% {
    transform: scale(1);
   }
  75% {
    transform: scale(1.4);
  }
  100% {
    transform: scale(1);
  }
}


@keyframes moveBack {
  0% {  
    transform: translateX(10px);
   }
  25% {
    transform: translateX(20px);
  }
  50% {
    transform: translateX(-20px);
   }
  75% {
    transform: translateX(30px);
  }
  100% {
    margin-left: 0px;
   transform: translateX(10px);
  }
}

