.header {
  padding: 20px 0;
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__list {
    @media (max-width: 420px) {
      display: none;
    }
  }
  &__item {
    &-body {
      display: flex;
      align-items: center;     
    }
  } 
  &__phone {
    color: var(--color-white);
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    margin-right: 18px;
    cursor: pointer;
    transition: color .3s ease-in-out;

    &:hover {
      color: var(--color-curiousBlue);
    }
  }
  &__link {
    &:not(:last-child) {
      margin-right: 10px;
      display: inline-block;
    }
  }
  &__icon {
    fill: var(--color-white);
    transition: .3s;
    cursor: pointer;
   
    & > circle {
      fill: none;
      stroke: var(--color-white);
      transition: .3s;
    }

    &:hover {
      fill: var(--color-curiousBlue);
      & > circle {
        fill: none;
        stroke: var(--color-curiousBlue);
      }
    }
  }


}
