.message {

   &__section {
      background-color: #0F131B;
      padding-top: 80px;
      padding-bottom: 55px;
      position: relative;

   }

   &__inner {
      max-width: 520px;
   }

   &__title {
      margin-bottom: 13px;
      opacity: 0;
      visibility: hidden;
      transform: translateX(-70px);
      transition: 1s;
   }

   &__subtitle {
      margin-bottom: 25px;
      font-weight: 300;
      font-size: 18px;
      line-height: 24px;
      color: var(--color-white);
      opacity: 0;
      visibility: hidden;
      transform: translateX(-70px);
      transition: 2s;
   }

   &__form {
      display: flex;
      flex-direction: column;
   }

   &__input {
      margin-bottom: 35px;
      background-color: #1C1B1F;
      color: var(--color-white);
      padding: 15px 30px;
      outline: none;
      border: none;
      border-radius: 5px;
      opacity: 0;
      visibility: hidden;
      transform: translateX(-70px);
      transition: 3s;

      &::placeholder {
         color: var(--color-white);
      }
   }

   &__btn {
      padding: 15px 60px;
      max-width: 200px;
   }


   &__decor-glasses {
      position: absolute;
      bottom: -70px;
      right: 20%;
      animation: rotate 30s infinite, moveLeftRight 20s infinite;
      @include mobile {
         display: none;
      }
   }

   &__decor-helmet-blue {
      position: absolute;
      top: -60px;
      right: -50px;
      animation: rotate2 50s infinite, moveBack 10s infinite;
      @include minidesk {
         right: 0;
         top: 0;
         width: 250px;
      }

      @include tablet {
         width: 200px;
      }

      @include mobile {
         width: 130px;
         top: -40px;
      }
   }
}